import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { Flex, Box, Text } from "rebass";
import GatsbyLink from 'gatsby-link';
import IndividualProduct from '../catalog/IndividualProduct';
import styled from '@emotion/styled/macro';

const StyledBox = styled(Box)`
  & img {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 80%;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const ProductRelated = (props) => {
  const { prodType, prodName } = props;

  const data = useStaticQuery(graphql`
    {
      relatedProducts: allShopifyProduct(limit: 4) {
        nodes {
          handle
          id
          title
          productType
          availableForSale
          tags
          fields {
            shopifyThemePath
            firstImage {
              originalSrc
            }
          }
          priceRange {
            maxVariantPrice {
              amount
            }
            minVariantPrice {
              amount
            }
          }
          variants {
            shopifyId
            availableForSale
            compareAtPrice
            price
          }
        }
      }
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            locales
            currency
          }
        }
      }
    }
  `)

  const { nodes } = data.relatedProducts;

  return (
    <Flex flexDirection={['column', 'row']}>
      {nodes.map((product, index) => {
        if (product.title !== prodName && product.productType === prodType) {
          return (
            <IndividualProduct key={index} product={product} />
          )
        } else {
          ""
        }
      })}
    </Flex>
  )
}

export default ProductRelated;
