import React, { useState } from 'react';
import { Flex, Box, Text } from 'rebass';
import styled from '@emotion/styled/macro';

const Image = styled.img`
  width: 70%;
`

function ProductPolicy() {
  return (
    <Box textAlign={'center'} fontSize={['3vw', '1.2vw', '1vw']}>
      <Flex mb={['5vw', '2vw', '1vw']}>
        <Box width={1 / 2}>
          <Image src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol1_100x52.png'}/>
          <Text my={['2vw', '1vw']}>Returns within 14 days</Text>
        </Box>

        <Box width={1 / 2}>
          <Image src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol2_100x52.png'}/>
          <Text my={['2vw', '1vw']}>Made in France</Text>
        </Box>
      </Flex>
      
      <Flex mb={['5vw', '2vw', '1vw']}>
        <Box width={1 / 2}>
          <Image src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol3_100x52.png'}/>
          <Text my={['2vw', '1vw']}>Customer service 9am to 6pm</Text>
        </Box>

        <Box width={1 / 2}>
          <Image src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol4_100x52.png'}/>
          <Text my={['2vw', '1vw']}>US free Delivengo shipping on orders over $100</Text>
        </Box>
      </Flex>
    </Box>
  );
}

export default ProductPolicy;